html,
body {
    margin: 0;
    padding: 0;
}

button {
    margin: 0;
    padding: 0;
    border: 0;
    background: none;
    font-size: 100%;
    vertical-align: baseline;
    font-family: inherit;
    font-weight: inherit;
    color: inherit;
    -webkit-appearance: none;
    appearance: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    line-height: 1.4em;
    background: #f5f5f5;
    color: #4d4d4d;
    min-width: 230px;
    max-width: 550px;
    margin: 0 auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-weight: 300;
}

:focus {
    outline: 0;
}

.hidden {
    display: none;
}

.info {
    margin: 65px auto 0;
    color: #bfbfbf;
    font-size: 0.8rem;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
    text-align: center;
}

.info p {
    line-height: 1;
}

.info a {
    color: inherit;
    text-decoration: none;
    font-weight: 400;
}

.info a:hover {
    text-decoration: underline;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
    .toggle-all,
    .todo-list li .toggle {
        background: none;
    }

    .todo-list li .toggle {
        height: 40px;
    }
}

@media (max-width: 430px) {
    .footer {
        height: 50px;
    }

    .filters {
        bottom: 10px;
    }
}

/* Mavo */

[property]:hover,
[mv-app] [mv-mode="edit"] .mv-editor {
    box-shadow: none !important;
}

[mv-app] [mv-mode="edit"] .mv-editor {
    width: 100% !important;
}

[property="todo"]:focus-within {
    border-bottom: 0;
}

[property="todo"] label:focus-within {
    padding: 0;
    margin: 0;
    background-image: none !important;
}

[property="todo"].completed label:focus-within {
    color: #4d4d4d;
    text-decoration: none;
}

[mv-app] [mv-mode="edit"] .mv-editor:focus {
    width: calc(100% - 78px) !important;
    padding: 14px 16px !important;
    margin: 0 0 0 43px !important;
    border: 1px solid #999 !important;
    box-shadow: inset 0 -1px 5px 0 rgba(0, 0, 0, 0.2) !important;
}

.mv-item-bar,
.mv-ui.mv-message,
button.mv-add,
meta,
[property="todo"] .view:focus-within .toggle,
[property="todo"] .view:focus-within button {
    display: none !important;
}
